import React, { Component } from "react";
import Csharp from '../images/CSharp.png';
import Asp from '../images/MVC.png';
import CSS from '../images/css.png';
import HTML from '../images/HTML.png';
import Jupyter from '../images/Jupyter.png';
import Matplotlib from '../images/Matplotlib.png';
import javascript from '../images/javascript.png';
import jQuery from '../images/jquery.png';
import numpy from '../images/numpy.png';
import react from '../images/React.png';
import VsStudio from '../images/Visual-Studio.png';
import python from '../images/python.png';
import Pandas from '../images/pandas-python.png';

class Skills extends Component {
  state = {};
  render() {
    return (
      <div className="main">
        <div className="page-header">
          <h3>Skills</h3>
        </div>

        <div className="box-container">
          <div className="box-content">
            <div className="Skills">
              <h6>Software Development</h6>
            </div>
            <div className="Programming-icon">
              <div>
                <img
                  src={Csharp}
                  height="100px "
                  width="100px"
                  alt="C Sharp"
                ></img>
                <span>
                  <h6>C Sharp</h6>
                </span>
              </div>

              <div>
                <img
                  src={react}
                  height="100px "
                  width="100px"
                  alt="React"
                ></img>
                <span>
                  <h6>React</h6>
                </span>
              </div>
              <div>
                <img
                  src={javascript}
                  height="100px "
                  width="100px"
                  alt="Javascript"
                ></img>
                <span>
                  <h6>Javascript</h6>
                </span>
              </div>
              <div>
                <img
                  src={jQuery}
                  height="100px "
                  width="100px"
                  alt="Jquery"
                ></img>
                <span>
                  <h6>Jquery</h6>
                </span>
              </div>
            
             
            </div>
            <div className="Programming-icon">
              <div>
                <img
                  src={Asp}
                  height="100px "
                  width="100px"
                  alt="Asp.Net MVC"
                ></img>
                <span>
                  <h6>Asp.Net MVC</h6>
                </span>
              </div>

              <div>
                <img
                  src={HTML}
                  height="100px "
                  width="100px"
                  alt="HTML"
                ></img>
                <span>
                  <h6>HTML</h6>
                </span>
              </div>

              <div>
                <img
                  src={CSS}
                  height="100px "
                  width="100px"
                  alt="CSS"
                ></img>
                <span>
                  <h6>CSS</h6>
                </span>
              </div>

              
              <div>
                <img
                  src={VsStudio}
                  height="100px "
                  width="100px"
                  alt="VS"
                ></img>
                <span>
                  <h6>Visual Studio</h6>
                </span>
              </div>
           </div>
          </div>
        </div>

        <div className="box-container">
          <div className="box-content">
            <div className="Skills">
              <h6>Data Science</h6>
            </div>
            <div className="Programming-icon">
            <div>
                <img
                  src={[python]}
                  height="100px "
                  width="100px"
                  alt="Python"
                ></img>
                <span>
                  <h6>Python</h6>
                </span>
              </div>
              <div>
                <img
                  src={Pandas}
                  height="100px "
                  width="100px"
                  alt="Pandas-Python"
                ></img>
                <span>
                  <h6>Pandas</h6>
                </span>
              </div>
              <div>
                <img
                  src={numpy}
                  height="100px "
                  width="100px"
                  alt="Numpy"
                ></img>
                <span>
                  <h6>Numpy</h6>
                </span>
              </div>

              <div>
                <img
                  src={Jupyter}
                  height="100px "
                  width="100px"
                  alt="Jupyter"
                ></img>
                <span>
                  <h6>Jupyter</h6>
                </span>
              </div>
              <div>
                <img
                  src={Matplotlib}
                  height="100px "
                  width="100px"
                  alt="Matplotlib"
                ></img>
                <span>
                  <h6>Matplotlib</h6>
                </span>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Skills;
